  function DBPedia2(props) {
    const { steps } = props;
    const ask = steps.ask.value;
    const [result,setResult] = useState("")
    const [loading,setLoad] = useState(true)
    const [trigger,setTrigger] = useState(false)
    const parseData = async()=> {
        const result = await axios.post(`${SERVER}/api/ask`,{text:ask});
        setResult(result.data)
        setLoad(false)
        
      }
    useEffect(() => {
        parseData()
      }, []);

      const triggetNext = () => {
        setTrigger(true);
        props.triggerNextStep();
      };

  return (
        <div className="dbpedia" >
          { loading ? <Loading /> : 
          <div style={{fontSize: 12}}>
            {result.data}
          </div>
          
          }
          {
          !loading &&
          <div
            style={{
              textAlign: 'center',
              marginTop: 20,
            }}
          >
            {
              !trigger &&
              <Button
                onClick={() => triggetNext()}
              >
                 
              </Button>
            }
          </div>
        }
        </div>
  )
}


const steps = [
    {
        id: '0',
        message: ' !',
 
        // This calls the next id
        // i.e. id 1 in this case
        trigger: '1',
    }, {
        id: '1',
 
        // This message appears in
        // the bot chat bubble
        message: '-      ...',
        trigger: '2'
    }, {
        id: '2',
 
        // Here we want the user
        // to enter input
        user: true,
        trigger: '3',
    }, {
        id: '3',
        message: ", {previousValue},      ?",
        trigger: '4'
    }, {
        id: '4',
        options: [
             
            { label: '  ',trigger:'5'},
            { label: '  ',trigger:'3' },
 
        ]
    },
    {
        id: '5',
        message: "  ...",
        trigger: 'ask'
    },
    {
        id: 'ask',
 
        // Here we want the user
        // to enter input
        user: true,
        trigger: '7',
    },
    {
        id: '7',
        component: <DBPedia2 />,
        waitAction: true,
        trigger: '3',
    }
];
//   const theme = {
