  // Set some properties of the bot
  const config = {
    botAvatar: chatBot,
    floating: true,
  };
function ChatBotComponent(props) {
    return (
       

                <ChatBot
                    // This appears as the header
                    // text for the chat bot
                    headerTitle=""
                    steps={steps}
                    {...config}
 
                />
          
     
    );
}

export default ChatBotComponent;